import React, {useState} from "react";
import AddCircleOutlineOutlinedIcon from '@material-ui/icons/AddCircleOutlineOutlined';
import DeleteIcon from '@material-ui/icons/Delete';
import {  Popup } from 'semantic-ui-react';


function CardStudAll(props){

    const[isAdded,setAdded]=useState(true);
    const[popContent,setContent]=useState("Added");

     function Add(){
         setAdded(true);
         setContent("Added");
     }
     function Delete(){
         setAdded(false);
         setContent("Deleted");
     }

    

    return(
        <div className="stud">
           <span className="name"><h3>{props.name}</h3></span> 
           <span className="roll"><h3 >{props.roll}</h3></span>  
          <div className="popup"> {isAdded ?
           ( <Popup
                content={popContent}
                on='click'
                closeOnTriggerMouseLeave
                pinned
                trigger={<button class="add-delete" onClick={Delete}>
            <DeleteIcon />
            </button>}/>
               
               ):(

                <Popup
                content={popContent}
                on='click'
                closeOnTriggerMouseLeave
                pinned
                trigger={<button class="add-delete" onClick={Add}>
                <AddCircleOutlineOutlinedIcon />  
                </button> }/>
                
            )
            }
            
            </div> 
        </div>
    )
}

export default CardStudAll;