import React from "react";
import Cardsub from "./CardSub"
import Item from "./subjectItem"




function Entry(subitem){
    return(
        <div className="entry">
        <Cardsub 
            key={subitem.key}
            img={subitem.imgURL}
            id={subitem.id}
            name={subitem.name}
            credit={subitem.credit}
            semester={subitem.sem}
        />
        </div>
    );
}

function Subject(){
    return(
        <div className="subject" id="sub">
        <h1 className="subject-heading">Subject</h1>
        <dl className="subject-detail">
        {Item.map(Entry)}
        </dl>
        </div>
    );

}

export default Subject;