import React, {useState} from "react";
import CardStud from "./cardstud"
import stud from "./Studentsem1"
import CardStudAll from "./CardStudAll"

function Student(stulist){
    return(
       <div className="stud-list">
           <CardStud
            name={stulist.name}
            roll={stulist.roll}
            />
       </div>
    );

}
function StudentAll(stulist){
    return(
       <div className="stud-list">
           <CardStudAll
            name={stulist.name}
            roll={stulist.roll}
            />
       </div>
    );

}



function Participant(){
    const[AllAdded,setAlladded]=useState(false);

    function AddAllParticipants(){
        setAlladded(true);
        alert("All students added...")
    }

    return(
        AllAdded?(
        <div id="participant">
            <h1 className="part-logo">Participants</h1>
            <h2>Subject : </h2>
            <div>{stud.map(StudentAll)}</div>
            
        </div>):(
            <div id="participant">
            <h1 className="part-logo">Participants</h1>
            <h2>Subject : </h2>
            <div>{stud.map(Student)}</div>
            <div ><button className="add-all" onClick={AddAllParticipants}>Add All Students</button></div>
        </div>
        )
    );
}

export default Participant;
